.play-list {
  color: #fff;
  list-style: none;
  font-size: 1.8rem;
  font-weight: 300;
}

.link-header {
  font-weight: 600;
  font-size: 2.2rem;
}

.second-list {
  list-style: none;
}

ul {
  padding: 2.5rem 0 2.5rem 0;
}

a {
  color: #fff;
  text-decoration: none;
  transition: all 0.4s;
}

a:hover {
  color: #fae6b8;
  text-decoration: none;
  cursor: pointer;
}

@media (min-width: 320px) and (max-width: 480px) {
  ul {
    text-align: center;
  }
}

